@import url(https://fonts.googleapis.com/css2?family=Arimo:wght@600&family=Kanit:ital@1&display=swap);
.gameBoard {
  display: grid;
  margin-left: 3rem;
  margin-top: 1.37rem;
  width: 70%;
}

.homePage_game {
  /* background-repeat: no-repeat;
  background-size: cover; */
  padding: 2rem 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  border-left: 1px solid rgb(228, 201, 163, 0.5);
  box-shadow: -2px 0px 3px rgba(216, 206, 206, 0.5);
  overflow:hidden;
}

.gameInfo {
  font-size: 1.4rem;
  text-shadow:  -1px -1px rgb(58, 55, 55), -1px 2px rgb(105, 103, 103);
  letter-spacing: 1px;
  width: 40%;
}

input[type=button]{
  width: 90%;
  height: 2.4rem;
  margin-bottom: 0.5rem;
  font-size: 1.05rem;
  font-family: 'Arimo', sans-serif;
  border: 2px solid rgb(243, 237, 237);
  border-radius: 4px;
  background-color: rgba(155, 147, 81, 0.7);
  color: rgb(243, 237, 237);
  cursor: pointer;
}
input[type=button]:hover {
  box-shadow:2px 2px rgb(173, 170, 170), -2px -2px rgb(173, 170, 170); 
}

.btn_disabled {
  cursor: not-allowed;
  pointer-events: none;
  color: grey !important;
  background-color: rgb(192, 185, 121, 0.3) !important;
  border: 2px solid gray !important;
}




.gameInfo-buttons-group1 {
  margin-bottom: 2rem;
}
.gameInfo-levels {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.gameInfo-time {
  color: rgb(161, 50, 50);
  text-shadow: 0px 0px;
}
.homePage_levels {
  padding: 2rem 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: -webkit-max-content;
  height: max-content;
}

.homePage_levels_level {
  width: 13rem;
  height: 40px;
  border-radius: 4px;
  background-color: rgb(20, 89, 146, 0.9);
  margin: 7px;
  cursor: pointer;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.homePage_levels_imgs {
  margin-right: 10px;
}

.homePage_levels_level img {
  width: 30px;
  margin: 5px 4px 0 2px
}
.homePage_levels_level p {
  margin: 11px 15px;
}

.homePage_levels_level:hover {
  box-shadow: 3px 0 3px rgba(46, 46, 46, 0.6), -3px 0 3px rgba(46, 46, 46, 0.6),
    0 3px 3px rgba(46, 46, 46, 0.6), 0 3px 3px rgba(46, 46, 46, 0.6);
}
.homePage_header {
  grid-column: 1/3;
  border-bottom: 1px solid rgb(228, 201, 163, 0.5);
  box-shadow: 0px 2px 2px rgba(216, 206, 206, 0.5);

  text-align: center;
  text-shadow:  -1px -1px rgb(58, 55, 55), -1px 2px rgb(105, 103, 103);

  font-size: 1.2rem;
}

